  --color-white: #ffffff;
  --color-light-grey: #f2f2f2;
  --color-pastel-blue: #a2cffe;
  --color-accent: #ff6f61;
  --color-text: #333333;
  --color-hover: #4b0082;
  --color-success: #4bb543;
}

html {
  background-color: var(--color-white);
  padding: 2rem 4rem;
  font-family: Inter, sans-serif;
  font-size: 10px;
}

@media screen and (max-width: 480px) {
  html {
    font-size: 12px;
  }
}

body {
  background-color: var(--color-light-grey);
  min-height: calc(100vh - 4rem);
  display: grid;
  grid-template-rows:
    auto
    1fr
    auto;
  justify-items: center;
  border-radius: 8px;
}

header {
  margin: 2rem 1rem;
}

h1 {
  font-size: 2.4rem;
}

main {
  padding: 1rem 3rem 2rem;
  width: 100%;
  height: 100%;
}

@media screen and (max-width: 480px) {
  html,
  main {
    padding: 1rem;
  }
}

footer {
  background-color: var(--color-pastel-blue);
  width: 100%;
  color: var(--color-text);
  padding: 0.5rem 0;
}

footer p {
  margin: 0 auto;
  width: fit-content;
  font-size: 1.8rem;
  font-weight: 500;
}

footer a {
  color: var(--color-text);
  text-decoration: none;
}

footer a:hover {
  color: var(--color-hover);
  text-decoration: underline;
}

button:not(.board-cell) {
  height: fit-content;
  width: fit-content;
  min-width: 150px;
  margin: 0 auto;
  padding: 14px 8px;
  border-radius: 4px;
  cursor: pointer;
  appearance: none;
  border: 1px solid var(--color-white);
  background-color: var(--color-pastel-blue);
  transition: all 0.3s ease-in-out;
  font-size: 18px;
  color: var(--color-text);
}

button:not(.board-cell):hover {
  border-color: var(--color-accent);
}

#game-container {
  display: grid;
  gap: 30px;
  grid-template-columns: 1fr 1fr;
}

#game-container.player-initial-form {
  grid-template-columns: 1fr 150px 1fr;
  grid-template-rows: 1fr;
  grid-template-areas: 'form1 button form2';
  align-items: center;
  justify-content: center;
}

.player-initial-form .player-1-container {
  grid-area: form1;
}

.player-initial-form #game-start-btn {
  grid-area: button;
}

.player-initial-form .player-2-container {
  grid-area: form2;
}

@media screen and (max-width: 1024px) {
  #game-container {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr;
  }
  #game-container.player-initial-form {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 50px 1fr;
    grid-template-areas: 'form1' 'button' 'form2';
  }
}

.player-container {
  height: 100%;
  width: 100%;
  min-height: 300px;
  padding: 0 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.player-name {
  font-weight: 600;
  font-size: 2.4rem;
}

.board-container {
  width: 100%;
  flex: 1;
  max-width: 500px;
  border: 1px solid black;
  margin: 2.5rem 0;
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  grid-template-rows: repeat(10, 1fr);
}

.board-cell {
  border: 1px solid black;
  aspect-ratio: 1;
  cursor: pointer;
}

.board-cell:not(.prompt-cell):hover,
.board-cell.selected,
.board-cell.touched {
  background-color: var(--color-accent);
}

.player .board-cell:hover:not(.hitted-ship, .missed-shot) {
  background-color: var(--color-pastel-blue);
}

.board-cell.disabled {
  cursor: not-allowed;
}

.enemy .missed-shot {
  background-color: var(--color-accent);
}

.player-ship {
  background-color: var(--color-pastel-blue);
}

.enemy .hitted-ship {
  background-color: var(--color-success);
}

.player .hitted-ship {
  background-color: var(--color-accent);
}

.player .missed-shot {
  background-color: var(--color-accent);
}

.form-player {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-top: 10px;
  background-color: rgba(255, 255, 255, 0.4);
  box-shadow: 2px 2px 10px 0 rgba(0, 0, 0, 0.3);
  padding: 4rem 2rem;
}

.form-player label,
.form-player input {
  display: inline-block;
}

.form-player div {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  margin-bottom: 10px;
}

.form-player label {
  font-size: 14px;
  font-weight: 500;
}

.form-player input {
  margin: 0;
  padding: 0.5rem;
  font-size: 14px;
  border-radius: 8px;
  padding: 0.5rem 1rem;
  border: 1px solid var(--color-text);
}

.popup {
  background: hsla(0, 0%, 30%, 0.5);
  position: fixed;
  height: 100vh;
  width: 100vw;
  transition: all 0.25s;
  top: 0;
  display: grid;
  justify-content: center;
  align-items: center;
  pointer-events: none;
  --opacity: 0;
  opacity: var(--opacity);
  grid-template-columns: 80%;
}

.popup.open {
  --opacity: 1;
  pointer-events: all;
}

.popup-wrapper {
  background-color: white;
  padding: 3rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 6px;
}

@media screen and (max-width: 768px) {
  .popup-wrapper {
    padding: 1rem;
  }
  .popup {
    grid-template-columns: 95%;
  }
}

.popup .board {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.popup .buttons-container {
  display: flex;
  flex-flow: row wrap;
  gap: 20px;
  justify-content: center;
  align-items: flex-start;
}

.popup .buttons-container button {
  font-size: 1.5rem;
  font-weight: 600;
  min-width: 100px;
  padding: 1rem 2rem;
}

.popup button[disabled] {
  cursor: not-allowed;
  opacity: 0.5;
}

.popup-player-name {
  font-size: 2.4rem;
}

.popup-player-name + p {
  font-size: 1.8rem;
}

.popup-player-name + p .strong {
  font-weight: 700;
}

.popup.dark {
  background: rgba(77, 77, 77, 1);
}

.popup-pass-turn .popup-wrapper {
  gap: 15px;
}

.popup-pass-turn .cta-text {
  font-weight: 500;
  font-size: 1.4rem;
}
